/* Body Principal */
.recover-password-body {
    font-family: 'Bacasime Antique', serif;
    margin: 0;
    padding: 0;
    background: white;
    height: 100vh;
    display: flex;
    flex-direction: column; /* Permite que los elementos se apilen verticalmente */
    justify-content: flex-start; /* Alinea el contenido desde la parte superior */
}

/* Navbar */
.mi-navbar {
    background-color: #007bff;
    padding: 15px 0;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000; /* Asegura que el navbar esté sobre el contenido */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.contenedor-navbar {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
}

.contenedor-principal {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.contenedor-titulo {
    flex: 1;
}

.navbar-brand p {
    font-size: 24px;
    color: white; /* Aseguramos que el texto sea blanco */
    margin: 0;
    font-weight: bold;
}

.contenedor-enlaces {
    display: flex;
    gap: 20px;
}

.enlace-navbar {
    color: white;
    text-decoration: none;
    font-size: 16px;
}

.enlace-navbar:hover {
    text-decoration: underline;
}

/* Contenedor de la recuperación de contraseña */
.recover-password-container {
    max-width: 400px;
    margin: 120px auto 0; /* Ajuste el margen superior para que no se solape con el navbar */
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    text-align: center;
}

/* Título */
.recover-password-container h1 {
    font-size: 24px;
    margin-bottom: 20px;
    color: #333;
}

/* Estilos del formulario */
.recover-password-container input {
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
}

.recover-password-container button {
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.recover-password-container button:disabled {
    background-color: #6c757d;
    cursor: not-allowed;
}

.recover-password-container button:hover:not(:disabled) {
    background-color: #0056b3;
}

/* Mensajes de error o éxito */
.registro-error-message {
    margin-top: 15px;
    font-size: 14px;
}

.registro-error-message.success {
    color: #68bd49;
}

.registro-error-message.error {
    color: #dc3545;
}
