@import "~leaflet/dist/leaflet.css";

/* Contenedor general de la vista de devoluciones */
.devolucion-page {
  font-family: 'Bacasime Antique', serif;
  background: linear-gradient(to bottom right, #f0f8ff, #e0f7fa);
  min-height: 100vh;
  padding: 40px 20px;
}

/* Contenedor central del formulario */
.devolucion-container {
  max-width: 700px;
  margin: auto;
  background-color: #ffffff;
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Título principal */
.devolucion-container h2 {
  text-align: center;
  font-size: 28px;
  font-weight: bold;
  color: #007BFF;
  margin-bottom: 25px;
}

/* Campo de número de factura */
.input-factura-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.input-factura {
  width: 100%;
  max-width: 400px;
  padding: 12px;
  border-radius: 8px;
  border: 1px solid #ccc;
  font-size: 1rem;
  margin-bottom: 12px;
}

/* Botón buscar factura */
.button-buscar {
  background-color: #007BFF;
  color: white;
  padding: 10px 25px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  transition: background-color 0.3s;
}

.button-buscar:hover {
  background-color: #0056b3;
}

/* Mensajes informativos */
.mensaje {
  text-align: center;
  color: #333;
  font-weight: bold;
  margin-top: 10px;
}

/* Detalles de factura encontrados */
.devolucion-detalles {
  margin-top: 30px;
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 10px;
}

.devolucion-detalles p {
  margin: 5px 0;
  font-size: 1rem;
}

/* Selector de producto a devolver */
.select-producto {
  width: 100%;
  padding: 10px;
  margin-top: 15px;
  margin-bottom: 20px;
  border-radius: 8px;
  border: 1px solid #ccc;
}

/* Comentarios del cliente */
.textarea-comentarios {
  width: 100%;
  padding: 10px;
  min-height: 100px;
  border-radius: 8px;
  border: 1px solid #ccc;
  resize: vertical;
  margin-bottom: 20px;
}

/* Botón para realizar la devolución */
.button-devolver {
  background-color: #28a745;
  color: white;
  padding: 10px 25px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  transition: background-color 0.3s;
}

.button-devolver:hover {
  background-color: #218838;
}

/* Mapa de ubicación (si se usa en esta vista) */
#map-devolucion {
  margin-top: 30px;
  height: 400px;
  border-radius: 10px;
  overflow: hidden;
}

/* Responsive */
@media (max-width: 600px) {
  .devolucion-container {
    padding: 20px;
  }

  .input-factura {
    width: 100%;
  }

  .button-buscar,
  .button-devolver {
    width: 100%;
    margin-top: 10px;
  }
}

